.form-wrapper {
    background: var(--dark);
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;

    & form {
        max-width: 30rem;
        // background: green;
        padding: 1rem;

        & h1 {
            padding-bottom: .5rem;
        }
    
        @at-root {
            label {
                padding-bottom: 1rem;
            }
        }
    
    }
    
}